const /** @type {?} */ MOUSE_EVENT_PROPERTIES = [
  'altKey', 'button', 'clientX', 'clientY', 'metaKey', 'movementX', 'movementY', 'offsetX',
  'offsetY', 'region', 'screenX', 'screenY', 'shiftKey'
];

const /** @type {?} */ KEYBOARD_EVENT_PROPERTIES = [
  'altkey', 'charCode', 'code', 'ctrlKey', 'isComposing', 'key', 'keyCode', 'location', 'metaKey',
  'repeat', 'shiftKey', 'which'
];

const /** @type {?} */ TRANSITION_EVENT_PROPERTIES = ['propertyName', 'elapsedTime', 'pseudoElement'];

const /** @type {?} */ EVENT_PROPERTIES = ['type', 'bubbles', 'cancelable'];

const /** @type {?} */ NODES_WITH_VALUE = new Set(
    ['input', 'select', 'option', 'button', 'li', 'meter', 'progress', 'param', 'textarea']);
/**
 * @param {?} e
 * @return {?}
 */
export function serializeGenericEvent(e: Event): {[key: string]: any} {
  return serializeEvent(e, EVENT_PROPERTIES);
}
/**
 * @param {?} e
 * @return {?}
 */
export function serializeEventWithTarget(e: Event): {[key: string]: any} {
  const /** @type {?} */ serializedEvent = serializeEvent(e, EVENT_PROPERTIES);
  return addTarget(e, serializedEvent);
}
/**
 * @param {?} e
 * @return {?}
 */
export function serializeMouseEvent(e: MouseEvent): {[key: string]: any} {
  return serializeEvent(e, MOUSE_EVENT_PROPERTIES);
}
/**
 * @param {?} e
 * @return {?}
 */
export function serializeKeyboardEvent(e: KeyboardEvent): {[key: string]: any} {
  const /** @type {?} */ serializedEvent = serializeEvent(e, KEYBOARD_EVENT_PROPERTIES);
  return addTarget(e, serializedEvent);
}
/**
 * @param {?} e
 * @return {?}
 */
export function serializeTransitionEvent(e: TransitionEvent): {[key: string]: any} {
  const /** @type {?} */ serializedEvent = serializeEvent(e, TRANSITION_EVENT_PROPERTIES);
  return addTarget(e, serializedEvent);
}
/**
 * @param {?} e
 * @param {?} serializedEvent
 * @return {?}
 */
function addTarget(e: Event, serializedEvent: {[key: string]: any}): {[key: string]: any} {
  if (NODES_WITH_VALUE.has(( /** @type {?} */((<HTMLElement>e.target))).tagName.toLowerCase())) {
    const /** @type {?} */ target = /** @type {?} */(( <HTMLInputElement>e.target));
    serializedEvent['target'] = {'value': target.value};
    if (target.files) {
      serializedEvent['target']['files'] = target.files;
    }
  }
  return serializedEvent;
}
/**
 * @param {?} e
 * @param {?} properties
 * @return {?}
 */
function serializeEvent(e: any, properties: string[]): {[key: string]: any} {
  const /** @type {?} */ serialized = {};
  for (let /** @type {?} */ i = 0; i < properties.length; i++) {
    const /** @type {?} */ prop = properties[i];
    ( /** @type {?} */((serialized as any)) /** TODO #9100 */)[prop] = e[prop];
  }
  return serialized;
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
